  background: -webkit-gradient(linear,left top, left bottom,from(#004E67),to(#242424));
  background: -webkit-linear-gradient(top,#004E67,#242424);
  background: -o-linear-gradient(top,#004E67,#242424);
  background: linear-gradient(180deg,#004E67,#242424);
  background-size: cover;
}

.header .header-bg{
  /* background: url('../images/header-bg.jpeg'); */
  width: 120%;
  position: absolute;
  left: -200px;
  top:-200px;
  height: 100vh;
}

.header .sjt{
  /* background: url('../images/sjt.png'); */
  /* background-repeat: no-repeat; */
  width: 100%;
  height: 100vh;
  text-align: center;
}

.header .sjt .sjt-image{
  bottom: 200px;
}

.header .header-bg{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
}

.header .header-bg .logo{
  width: 40%;
  -ms-flex-item-align: center;
      align-self: center;
  position: relative;
  left: 25%;
}

@media(max-width:800px){
  .header .sjt{
    top:10px;
  }
  .header .sjt .sjt-image{
    top:200px;
  }
