  }

}

.header .tt{
  /* background: url('../images/tt.png'); */
  /* background-repeat: no-repeat; */
  width: 100%;
  height: 100vh;
}

.register-button-container{
  width: 100%;
  position: relative;
  top:-160px;
  z-index: 10;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}
