.featured-container{
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 250px;
  overflow-x: scroll;
  overflow-y: hidden;
  color: #ffffff;

}
.featured-container1::-webkit-scrollbar {
    height: 0px;  /* remove scrollbar space */
    background: transparent;  /* optional: just make scrollbar invisible */
}

.featured-container .phenomenon{
  margin: 10px;
  width: 400px;
  position: absolute;
  left: 100px;
}

.featured-container .phenomenon h2{
  font-weight: 300;
}

.event-card{
  background: #232323;
  width: auto;
  width: 200px;
  margin-top:20px;
  padding: 20px;
  text-align: center;
  margin-right:20px;
  overflow: hidden;
  -webkit-transition: all 0.3s cubic-bezier(0.4, 0, 1, 1);
  -o-transition: all 0.3s cubic-bezier(0.4, 0, 1, 1);
  transition: all 0.3s cubic-bezier(0.4, 0, 1, 1);
  display: -ms-flexbox;
  display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}
.featured-container .event-card:hover{
    cursor: pointer;
    background-size: cover;
}

.featured-container .event-card{
  height: 180px;
  position: relative;
  left: 600px;
  z-index: 3;
}

.event-card .overlay-layer{
  position: absolute;
  top:0px;
  left: 0px;
  -webkit-transition: all 0.3s cubic-bezier(0.4, 0, 1, 1);
  -o-transition: all 0.3s cubic-bezier(0.4, 0, 1, 1);
  transition: all 0.3s cubic-bezier(0.4, 0, 1, 1);
  background: rgba(46,46,46,0.8);
  width: 100%;
  height: 100%;
  z-index: -1;
}
.featured-container .event-card:hover .overlay-layer{
  background:rgba(46,46,46,0.6);

}
.event-card-img{
  position: absolute;
  top:0px;
  -webkit-transition: all 0.3s cubic-bezier(0.4, 0, 1, 1);
  -o-transition: all 0.3s cubic-bezier(0.4, 0, 1, 1);
  transition: all 0.3s cubic-bezier(0.4, 0, 1, 1);
  left:0px;
  width:auto;
  height: 100%;
  z-index: -2;
}
.event-card:hover .event-card-img{
  -webkit-transform: scale(1.2);
      -ms-transform: scale(1.2);
          transform: scale(1.2);
}
.category-title{
  font-size:24px;
  text-align: center;
}
.category-subtitle{
    font-size: 20px;
    color:#ddd;
}
@media (max-width: 600px) {
  .featured-container .phenomenon{
    left: 5px;
    width: 300px;
  }
  .featured-container .event-card{
    left: 300px;
  }
}

