}
.footer-right-col{
    border-left: 1px solid #414043;
    padding: 10px 30px;
}
.copyrighttext{
  color:#9e9e9e;
  margin-top: 0px;
  display: inline-block;
  position: relative;
  top:0px;
}
.developerstext{

}
@media(max-width: 600px){
  footer button{
