  }
}
.arrow{
  position: absolute;
  top:120vh;
  cursor: pointer;
  padding: 20px;
  background: rgba(0,0,0,0.5);
}

.left{
  left: 0px;
  padding-top: 20px;
  z-index: 10;
}

.right{
  right: 0px;
  padding-top: 20px;
  z-index: 10;
}

@media handheld {
  .arrow{
    display: none;
  }
}

@media(max-width:600px){
  .arrow{
    display: none;
  }
}

.visible{
  visibility: visible;
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
}

.hidden{
  visibility: hidden;
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
}

.countdown{
  width:100%;
  position: relative;
  top:-50px;
  padding-top: 100px;
  padding-bottom: 60px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.copyright-mobile-col{
  display: none;
}

@media (max-width:769px){
  .copyright-row{
    display: none;
  }
  .copyright-mobile-col{
    text-align: center;
    display: block;
  }
  .footer-right-col{
    text-align: center;
    padding-top: 0;
  }
  .footer-left-col{
    text-align: center;
    padding-bottom: 0;
  }
  .laptop-only-br{
    display: none;
  }
}



