.header-campus{
   display: -ms-flexbox;
   display: flex;
   -ms-flex-pack:center;
       justify-content:center;
   -ms-flex-align: center;
       align-items: center;
   background: -webkit-gradient(
      linear,
      left top, left bottom,
      from(rgba(20,20,20, .5)),
      to(rgba(20,20,20, .5))),
      url('../images/campus-ambassador.jpg') center 0px / cover;
   background: -webkit-linear-gradient(
      rgba(20,20,20, .5),
      rgba(20,20,20, .5)),
      url('../images/campus-ambassador.jpg') center 0px / cover;
   background: -o-linear-gradient(
      rgba(20,20,20, .5),
      rgba(20,20,20, .5)),
      url('../images/campus-ambassador.jpg') center 0px / cover;
   background: linear-gradient(
      rgba(20,20,20, .5),
      rgba(20,20,20, .5)),
      url('../images/campus-ambassador.jpg') center 0px / cover;
   }
.campus-ambassador-page h1,h3{
  font-weight: 400;
  color: #fff;
  width: auto;
}

p.description{
  text-align: justify;
}
.table-row{
  border-bottom: 1px solid #9E9E9E !important;
}

@media (max-width:600px) {
  .table-container table{
    min-width: 700px;
  }
  .table-container>div>div{
    overflow-x: scroll !important;
  }
}



